* {
    margin: 0;
    padding: 0;;
    box-sizing: border-box;
    // outline: 1px solid black;
}

body {
    width: 100%;
    background: $neutral-black;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
}

header {
    height: fit-content;
    width: 100%;
    border-bottom: 1px solid transparentize($color: $neutral-white, $amount: .5);
    display: flex;
    justify-content: center;
    align-items: flex-start;

    .logo-container {
        width: 100%;
        max-width: 1200px;
    }
    
    .logo {
        margin: 20px 48px;
    }
}

main {
    height: 618px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    .title {
        position: relative;
        margin-bottom: 18px;
    }

    .text-bg {
        position: absolute;
        z-index: -1;
        height: 24px;
        width: calc(100% + 32px);
        bottom: 6px;
        left: 50%;
        transform: translateX(-50%);
        // background-color: $horizontal-gradient;
        background: $horizontal-gradient;
    }

    p {
        max-width: 360px;
    }
}

.l-tools {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    .languages {
        border-bottom: 1px solid transparentize($color: $neutral-white, $amount: .5);
    }

    .section-title {
        position: relative;
    }

    h2 {
        margin: 96px 0 5px 0;
    }

    .text-bg {
        position: absolute;
        z-index: -1;
        height: 24px;
        width: calc(100% + 32px);
        bottom: -5px;
        left: 50%;
        transform: translateX(-50%);
        background: $horizontal-gradient;
    }
}

footer {
    width: 100%;
    border-top: 1px solid transparentize($color: $neutral-white, $amount: .5);  
}